// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import App from './App'
import Vue from 'vue';
import Vuex from 'vuex'
import iView from 'iview'
import Routers from './router'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import 'babel-polyfill'
import 'core-js/modules/es6.object.assign'

import jQuery from 'jquery'
import util from './assets/script/util'
import JsonViewer from "vue-json-viewer"
import CommonUtil from "./assets/script/utils/CommonUtil";
//CSS
import 'iview/dist/styles/iview.css'
import "./assets/css/custom.css"
Vue.use(VueRouter);
Vue.use(iView);
Vue.use(JsonViewer);
Vue.use(Vuex);
Vue.prototype.$util = util;
Vue.prototype.$jq = jQuery;
const store = new Vuex.Store({
  state:{
    userinfo:''
  },
  mutations: {
    setUserInfo(state,id){
      state.userinfo=id
    }
  }
});
Vue.prototype.$store=store;
// 路由配置
const RouterConfig = {
  mode: 'history',
  routes: Routers
};
const router = new VueRouter(RouterConfig);
const getLastUrl=(str,yourStr)=>str.slice(str.indexOf(yourStr));//取到浏览器出现网址的最后"/"出现的后边的字符
router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  // 判断该路由是否需要登录权限
  if (to.matched.some(record => record.meta.requireAuth)) {
    // 判断当前的token是否存在 ； 登录存入的token
    if (sessionStorage.access_token) {
        if(!store.state.userinfo.id){
          store.commit("setUserInfo",{id:110233435435});
          CommonUtil.DynamicRouters(this,function(routers){
            router.addRoutes(routers);
            next({path:sessionStorage.currentPagePath})
          })
        }else{
          next()
        }
    } else {
      //sessionStorage.clear()
      next({
        path: '/login',
        // 将跳转的路由path作为参数，登录成功后跳转到该路由
        query: {redirect: to.fullPath}
      })
    }
  } else {
    next();
  }
});
router.afterEach((to, from, next) => {
  iView.LoadingBar.finish();
  sessionStorage.setItem("currentPagePath",to.path);
  window.scrollTo(0, 0)
});

FastClick.attach(document.body);

Vue.config.productionTip = false;



/* eslint-disable no-new */
new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app');



