<template>
  <div class="layout" style="height: 100%; width: 100%;margin: 0px;padding: 0px;border: 0px;
  background-size: 100% 100%;">
    <Layout style="background-size: 100% 100%;"
    :style="{backgroundImage:'url(../static/bg/'+bgimg+')'}"
    >
      <!--内容-->
      <Content ref="content" style="min-width: 1000px" :style="{padding:'0px',height:clientHeight+'px',marginBottom:0}">
        <router-view style="min-width: 1000px"  v-on:ListenContent="listenMethod"></router-view>
      </Content>
      <!--尾部-->
      <!--<Footer class="layout-footer-center" style="background: black;height: 70px">2019-2025 &copy; TalkingData</Footer>-->
    </Layout>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        bgimg:"bg3.jpg",
        headerRegister:true,
        headerLogin:true,
        headerUserInfo:false,
        clientHeight:`${document.documentElement.clientHeight}`,
        formInline: {
          user: '',
          password: ''
        },
        ruleInline: {
          user: [
            {required: true, message: '请输入账号', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
            {type: 'string', min: 6, message: '至少有6位密码', trigger: 'blur'}
          ]
        }
      }
    },
    mounted() {
      const userinfo=sessionStorage.getItem("userinfo")
      if(userinfo!=null){
         this.headerLogin=false
         this.headerRegister=false
         this.headerUserInfo=true
      }
      // 获取浏览器可视区域高度
      this.clientHeight = `${document.documentElement.clientHeight}`;
      //document.body.clientWidth;
      //console.log(self.clientHeight);
      window.onresize = function temp() {
        this.clientHeight = `${document.documentElement.clientHeight}`;
        console.log(this.clientHeight);
      };
    },
    watch: {
      // 如果 `clientHeight` 发生改变，这个函数就会运行
      clientHeight: function () {
        this.changeFixed(this.clientHeight)
      }
    },
    methods: {
      listenMethod:function (fun) {
         fun(this)
      },
      changeFixed(clientHeight) {                        //动态修改样式
        console.log(clientHeight);
        this.$refs.content.style.height = clientHeight ;
      },
      logout() {
        const that=this;
        this.$util.req("/api/security/user/logout")
          .setSuccessfn(function (result) {
            if(result.code===1000){
              sessionStorage.removeItem("userinfo")
              sessionStorage.removeItem("token")
              that.headerLogin=true
              that.headerUserInfo=false
              that.headerRegister=true
              that.$router.push({path:"/login"})
            }
          })
          .setErrorfn(function (result) {
            sessionStorage.removeItem("userinfo")
            sessionStorage.removeItem("token")
            that.headerLogin=true
            that.headerUserInfo=false
            that.headerRegister=true
            that.$router.push({path:"/login"})
          })
          .go()
      }
    }
  }
</script>
<style scoped>
  .custom-input-size-40 > input {
    height: 43px;
    font-size: 17px;
  }

  .custom-card {
    width: 350px;
    padding: 10px;
    border-left: 0px;
    border-right: 0px;
    /*box-shadow:0 0 12px #f9fcff inset;*/
  }

  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }

  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }

  .layout-footer-center {
    text-align: center;
  }
</style>



