import ReqUtil from "./ReqUtil";

const DynamicRouters = function (that,callback) {
  ReqUtil.doPost("/api/menuService/routes")
    .setSuccessFunction(function (result) {
      if(result.success){
        let indexChildRouter = [];
        let router = result.data;
        for (let i in router) {
          if (router[i].viewPath) {
            indexChildRouter.push({
              path: router[i].viewPath,
              meta: {title: router[i].name, requireAuth: true},
              component: (resolve) => require(['../../../views' + router[i].viewPath], resolve)
            })
          }
        }
        indexChildRouter.push(
          {
            path: '/index/404',
            meta: {title: '无此页面'},
            component: (resolve) => require(['../../../views/index/404'], resolve)
          },
          {path: '*', redirect: "/index/404"}
        )
        let dynamicRouters = [{
          path: '/index',
          meta: {title: '主页面', requireAuth: true},
          children: indexChildRouter,
          component: (resolve) => require(['../../../views/index'], resolve)
        }]
        // sessionStorage.setItem("dynamicrouters",JSON.stringify(dynamicRouters))
        callback(dynamicRouters)
      }
    })
    .setCompleteFunction(function (result) {
      if((result.status||result.code)===401){
        //window.location.href='/login'
      }
    })
    .execute()
};

export default {DynamicRouters}



