const routers = [
  {
    path: '/',
    meta: {title: '首页', requireAuth: true},
    component: (resolve) => require(['./views/home'], resolve)
  },
  {
    path: '/login',
    meta: {title: '登录'},
    component: (resolve) => require(['./views/login'], resolve)
  },
  {
    path: '/loginSucess',
    meta: {title: '登录成功'},
    component: (resolve) => require(['./views/loginSucess'], resolve)
  },
  {
    name:"bindPhone",
    path: '/bindPhone',
    meta: {title: '绑定手机号'},
    component: (resolve) => require(['./views/bindPhone'], resolve)
  },
  {
    name:"myResume",
    path: '/myResume',
    meta: {title: '我的简历'},
    component: (resolve) => require(['./views/myResume'], resolve)
  },
  {
    path: '/register',
    meta: {title: '注册用户'},
    component: (resolve) => require(['./views/register'], resolve)
  },
  {
    path: '/index',
    meta: {title: '主页面', requireAuth: true},
    component: (resolve) => require(['./views/index'], resolve),
    children: [
      {
        path: '/index/404',
        meta: {title: '页面找不到'},
        component: (resolve) => require(['./views/index/404'], resolve)
      },
      {
        path: '/index/indexHome',
        meta: {title: '控制台首页'},
        component: (resolve) => require(['./views/index/indexHome'], resolve)
      },
      {
        path: '/index/userinfo',
        meta: {title: '用户管理'},
        component: (resolve) => require(['./views/index/userInfo'], resolve)
      },
      {
        path: '/index/roleinfo',
        meta: {title: '角色管理'},
        component: (resolve) => require(['./views/index/roleInfo'], resolve)
      },
      {
        path: '/index/menuinfo',
        meta: {title: '角色管理'},
        component: (resolve) => require(['./views/index/menuInfo'], resolve)
      },
      {
        path: '/index/dictinfo',
        meta: {title: '数据字典'},
        component: (resolve) => require(['./views/index/dictInfo'], resolve)
      },
      {path: '*', redirect: "/index/404"}
    ]
  },
  {path: '*', redirect: "/"}
]
export default routers



